<template>
  <div class="container">
    <h3>directives</h3>
    <ul>
      <li>
        <strong>:</strong>
        <ol>
          <li><code>Object</code></li>
        </ol>
      </li>
      <li><strong class="warning">:</strong>
        1directivedirectives 2directive  bind  inserted
      </li>
      <li>
        <strong>:</strong>
        <ol>
          <li><code>bind</code></li>
          <li><code>inserted</code> document </li>
          <li><code>update</code> VNode <strong> VNode </strong> ()</li>
          <li><code>componentUpdated</code> VNode <strong> VNode</strong> </li>
          <li><code>unbind</code> </li>
        </ol>
      </li>
      <li>
        <strong></strong>
        <ol>
          <li>el:  DOM </li>
          <li>
            binding: 
            <ol>
              <li><strong>name:</strong>  <code>v-</code> </li>
              <li><strong>value: </strong>  <code>v-my-directive="1 + 1"</code>, value  2</li>
              <li><strong>oldValue: </strong> <code>update</code> <code>componentUpdated</code> </li>
              <li><strong>expression: </strong>  <code>v-my-directive="1 + 1"</code>  <code>expression  "1 + 1"</code></li>
              <li><strong>arg: </strong> <code>v-my-directive:foo arg  "foo"</code></li>
              <li><strong>modifiers: </strong>  <code>v-my-directive.foo.bar</code>,  modifiers  <code>{ foo: true, bar: true }</code></li>
            </ol>
          </li>
          <li>vnode: Vue  <a href="https://cn.vuejs.org/v2/api/#VNode" target="_blank">VNode API</a> </li>
          <li>oldVnode:  update  componentUpdated </li>
        </ol>
      </li>
      <li>
        <strong class="warning">:</strong>
         <code>el</code>  <strong>dataset</strong> 
      </li>
      <li>
        <strong class="warning">:</strong>
        DOMvueDOM
      </li>
    </ul>
    <ul>
      <span class="warning"></span>
      <ol>
        <li><code>1</code></li>
        <li><code>2</code></li>
        <li><code>3</code></li>
        <li><code>4</code></li>
        <li><code>5</code></li>
        <li><code>6</code></li>
      </ol>
    </ul>
    <h5>1:</h5>
    <pre>
      <code class="html">
 &lt;input type=&quot;text&quot; v-maxlength=&quot;demo1.str&quot; v-model=&quot;demo1.str&quot;&gt;
      </code>
    </pre>
    <pre>
      <code class="js">
  export default {
    props: [],
    data() {
      return {
        demo1: {
          str: ''
        }
      }
    },
    directives: {
      // 
      maxlength: {
        bind(el, binding, vnode) {
          // console.log('bind')
        },
        inserted() {
          // console.log('inserted')
        },
        update(el, binding, vnode) {
          // el.value = ''
         // console.log(binding.value)
          let str = binding.value
          if(str.length >= 10) {
            el.value = str.substr(0, 10)
          }
        },
        componentUpdated() {
          // console.log('componentUpdated')
        },
        unbind() {
          // console.log('unbind')
        }
      }
    }
  }
      </code>
    </pre>

    <div class="demo-box">
      <input type="text" v-maxlength="demo1.str" v-model="demo1.str" placeholder="10">
    </div>

    <h5>2: </h5>
    <pre>
      <code class="html">
&lt;div v-demo=&quot;{ color: &#x27;white&#x27;, text: &#x27;hello!&#x27; }&quot;&gt;
      </code>
    </pre>
    <pre>
      <code class="js">
export default {
    props: [],
    data() {
      return {}
    },
    mounted() {
    },
    directives: {
      // 
      demo: {
        bind(el, binding, vnode) {
          $(el).children('span').eq(0).html(binding.value.color)  // white
          $(el).children('span').eq(1).html(binding.value.text)   // hello!
        },
        inserted() {
          // console.log('inserted')
        },
        update() {
          // console.log('update')
        },
        componentUpdated() {
          // console.log('componentUpdated')
        },
        unbind() {
          // console.log('unbind')
        }
      }
    }
  }
      </code>
    </pre>
    <div class="demo-box">
      <div v-demo="{ color: 'white', text: 'hello!' }">
        <p>json</p>
        <span></span>
        <span></span>
      </div>
    </div>

    <h5>3: </h5>
    <div class="demo-box">
      <input type="text" placeholder="" v-model="numberOnly" v-numberonly>
    </div>
    <pre>
      <code class="html">
&lt;input type=&quot;text&quot; placeholder=&quot;&quot; v-model=&quot;numberOnly&quot; v-numberonly&gt;
      </code>
    </pre>
    <pre>
      <code class="js">
numberonly: {
    update(el, binding, vnode) {
      let val = el.value
      el.value = val.replace(/\D+/, '')
    }
  }
      </code>
    </pre>

    <h5>4: </h5>
    <div class="demo-box">
      <div class="number-box">
        <input type="text" name="number1" v-autofocus="0" v-model="demo5.num0" maxlength="1">
        <input type="text" name="number2" v-autofocus="1" v-model="demo5.num1" maxlength="1">
        <input type="text" name="number3" v-autofocus="2" v-model="demo5.num2" maxlength="1">
        <input type="text" name="number4" v-autofocus="3" v-model="demo5.num3" maxlength="1">
        <input type="text" name="number5" v-autofocus="4" v-model="demo5.num4" maxlength="1">
        <input type="text" name="number6" v-autofocus="5" v-model="demo5.num5" maxlength="1">
        <input type="text" name="number7" v-autofocus="6" v-model="demo5.num6" maxlength="1">
        <input type="text" name="number8" v-autofocus="7" v-model="demo5.num7" maxlength="1">
      </div>
    </div>
    <pre>
      <code class="html">
&lt;div class=&quot;number-box&quot;&gt;
   &lt;input type=&quot;text&quot; name=&quot;number1&quot; v-autofocus=&quot;0&quot; v-model=&quot;demo5.num0&quot; maxlength=&quot;1&quot;&gt;
   &lt;input type=&quot;text&quot; name=&quot;number2&quot; v-autofocus=&quot;1&quot; v-model=&quot;demo5.num1&quot; maxlength=&quot;1&quot;&gt;
   &lt;input type=&quot;text&quot; name=&quot;number3&quot; v-autofocus=&quot;2&quot; v-model=&quot;demo5.num2&quot; maxlength=&quot;1&quot;&gt;
   &lt;input type=&quot;text&quot; name=&quot;number4&quot; v-autofocus=&quot;3&quot; v-model=&quot;demo5.num3&quot; maxlength=&quot;1&quot;&gt;
   &lt;input type=&quot;text&quot; name=&quot;number5&quot; v-autofocus=&quot;4&quot; v-model=&quot;demo5.num4&quot; maxlength=&quot;1&quot;&gt;
   &lt;input type=&quot;text&quot; name=&quot;number6&quot; v-autofocus=&quot;5&quot; v-model=&quot;demo5.num5&quot; maxlength=&quot;1&quot;&gt;
   &lt;input type=&quot;text&quot; name=&quot;number7&quot; v-autofocus=&quot;6&quot; v-model=&quot;demo5.num6&quot; maxlength=&quot;1&quot;&gt;
   &lt;input type=&quot;text&quot; name=&quot;number8&quot; v-autofocus=&quot;7&quot; v-model=&quot;demo5.num7&quot; maxlength=&quot;1&quot;&gt;
&lt;/div&gt;
      </code>
    </pre>
    <pre>
      <code class="js">
autofocus: {
  bind(el, binding, vnode) {

  },
  inserted(el, binding, vnode) {
    let parentNode = el.parentNode
    let inputArr = parentNode.querySelectorAll('input')
    // TODO input
    el.addEventListener('keydown', function (e) {
      let index = binding.value
      if (e.keyCode === 8 && el.value.length === 0 & index > 0) {
        console.log(index)
        // 
        inputArr[--index].focus()
      }
    })
    el.addEventListener('keyup', function (e) {
      let val = el.value
      let index = binding.value
      el.value = val.replace(/\D+/, '')
      // 
      if (el.value.length === 1 && index < 7 && e.keyCode !== 8) {
        // 
        inputArr[++index].focus()
      }
    })
  },
  update(el, binding, vnode) {

  },
  componentUpdated() {
    // console.log('componentUpdated')
  },
  unbind() {
    // console.log('unbind')
  }
}
      </code>
    </pre>

    <h5>5: </h5>
    <div class="demo-box">
      <clickoutsideComponent></clickoutsideComponent>
    </div>
    <pre v-pre>
      <code>
&lt;template&gt;
  &lt;div class=&quot;main&quot; v-clickoutside=&quot;handlerClose&quot;&gt;
    &lt;button @click=&quot;show = !show&quot; class=&quot;btn&quot;&gt;&lt;/button&gt;
    &lt;div class=&quot;dropdown&quot; v-show=&quot;show&quot;&gt;
      &lt;p&gt;&lt;/p&gt;
    &lt;/div&gt;
  &lt;/div&gt;
&lt;/template&gt;

&lt;script&gt;
  import clickoutside from &#x27;./clickoutside.js&#x27;
  export default {
    name: &#x27;clickoutside&#x27;,
    directives: {
    // 
      clickoutside: clickoutside
    },
    data () {
      return {
        show: false
      }
    },
    methods: {
      handlerClose () {
        this.show = false
      }
    }
  }
&lt;/script&gt;

&lt;style scoped lang=&quot;stylus&quot;&gt;
.main
  width: 125px
  .btn
    display: block
    width: 100%
    color: #fff
    background-color: #39f
    border: 0
    padding: 6px
    text-align: center
    font-size: 12px
    border-radius: 4px
    cursor: pointer
    outline: none
    position: relative
  .btn:active
    top: 1px
    left: 1px
  .dropdown
    width: 100%
    height: 150px
    margin: 5px 0
    font-size: 12px
    background-color: #fff
    border-radius: 4px
    box-shadow: 0 1px 6px rgba(0,0,0,.2)
    p
      display: inline-block
      padding: 6px
&lt;/style&gt;
      </code>
    </pre>

    <pre v-pre>
      <code>
// clickoutside.js 
// 
export default {
  bind (el, binding, vnode) {
    function documentHandler (e) {
      // contains AB
      // 
      if (el.contains(e.target)) {
        return false
      }
      // 
      if (binding.expression) {
        //  methods 
        binding.value(e)
      }
    }
    // this.xxxel.__vueClickOutside__documentHandlerunbinddocumentclick
    el.__vueClickOutside__ = documentHandler
    document.addEventListener(&#x27;click&#x27;, documentHandler)
  },
  unbind (el, binding) {
    // 
    document.removeEventListener(&#x27;click&#x27;, el.__vueClickOutside__)
    delete el.__vueClickOutside__
  }
}

      </code>
    </pre>

    <h5>6: </h5>
    <div class="demo-box">
      <timeComponent></timeComponent>
    </div>
    <pre v-pre>
   <code>
&lt;!-- vue --&gt;
&lt;template&gt;
  &lt;div&gt;
    &lt;div v-time=&quot;timeNow&quot;&gt;&lt;/div&gt;
    &lt;div v-time=&quot;timeBefore&quot;&gt;&lt;/div&gt;
  &lt;/div&gt;
&lt;/template&gt;

&lt;script&gt;
  import time from &#x27;./time.js&#x27;
  export default {
    directives: {
    // 
      time: time
    },
    data () {
      return {
        timeNow: (new Date()).getTime(),
        // 2018/1/8
        timeBefore: 1488930695721
      }
    },
    mounted () {
    },
    methods: {
    }
  }
&lt;/script&gt;

   </code>
 </pre>
    <pre v-pre>
      <code>
// time.js
// 
import timeFn from './timeFn.js'
export default {
  // bind,v-timebinding.valueTime.getFormatTime()el.innerHTMLel.__timeoutunbind
  bind (el, binding, vnode) {
    el.innerHTML = timeFn.getFormatTime(binding.value)
    el.__timeout__ = setInterval(function () {
      el.innerHTML = timeFn.getFormatTime(binding.value)
    }, 6000)
  },
  unbind (el, binding) {
    clearInterval(el.__timeout__)
    delete el.__timeout__
  }
}

      </code>
    </pre>
    <pre v-pre>
      <code>
// timeFn.js
let time = {}
// 
time.getUnix = () => {
  let date = new Date()
  return date.getTime()
}

// 000
time.getTodayUnix = () => {
  let date = new Date()
  date.setHours(0)
  date.setMinutes(0)
  date.setSeconds(0)
  date.setMilliseconds(0)
  return date.getTime()
}

// 11000
time.getYearUnix = () => {
  let date = new Date()
  date.setMonth(0)
  date.setDate(1)
  date.setHours(0)
  date.setMinutes(0)
  date.setSeconds(0)
  date.setMilliseconds(0)
  return date.getTime()
}

// 
time.getLastDate = (time) => {
  let date = new Date(time)
  let month = date.getMonth() + 1 < 10 ? '0' + (date.getMonth() + 1) : date.getMonth() + 1
  let day = date.getDate() < 10 ? '0' + date.getDate() : date.getDate()
  return date.getFullYear() + '-' + month + '-' + day
}

// 
time.getFormatTime = (timestamp) => {
  let now = time.getUnix() // 
  let today = time.getTodayUnix() // 0
  let year = time.getYearUnix() // 0
  let timer = (now - timestamp) / 1000 // 
  let tip = ''
  if (timer <= 0) {
    tip = ''
  } else if (Math.floor(timer / 60) <= 0) {
    tip = ''
  } else if (timer < 3600) {
    tip = Math.floor(timer / 60) + ''
  } else if (timer >= 3600 && (timestamp - today >= 0)) {
    tip = Math.floor(timer / 3600) + ''
  } else if (timer / 86400 <= 31) {
    tip = Math.ceil(timer / 86400) + ''
  } else {
    tip = time.getLastDate(timestamp)
  }
  return tip
}

export default time

      </code>
    </pre>
  </div>
</template>
<style scoped>
  .number-box input{
    font-weight: bold;
    text-align: center;
    font-size: 15px;
    width: 30px;
    height: 40px;
  }
</style>
<script>
  import maxlength from './maxlength.js'
  import parameter from './parameter.js'
  import numberonly from './numberonly.js'
  import autofocus from './autofocus.js'
  import clickoutsideComponent from './clickoutside/clickoutside.vue'
  import timeComponent from './time/time.vue'
  export default {
    props: [],
    data () {
      return {
        demo1: {
          str: ''
        },
        numberOnly: '',
        demo5: {
          num: '',
          num0: '',
          num1: '',
          num2: '',
          num3: '',
          num4: '',
          num5: '',
          num6: '',
          num7: ''
        }
      }
    },
    mounted () {
    },
    directives: {
      // 
      maxlength: maxlength,
      demo: parameter,
      // 
      numberonly: numberonly,
      // 
      autofocus: autofocus
    },
    methods: {
    },
    components: {
      clickoutsideComponent,
      timeComponent
    },
    watch: {}
  }
</script>



