<template>
  <div class="container">
    <h5>tab</h5>
    <ul>
      <li>
         
         <ol>
           <li>1</li>
           <li>2paneslot</li>
           <li>3$parent$children: prop: value data: currentValue, this.$emit('on-click', name)</li>
         </ol>
      </li>
    </ul>
    <br><br>
    <div class="demo-box">
      <tabs v-model="activeKey">
        <pane label="" name="1">
          
        </pane>

        <pane label="" name="2">
          
        </pane>

        <pane label="" name="3">
          
        </pane>
      </tabs>
    </div>
    <h5></h5>
    <pre v-pre>
      <code>
&lt;template&gt;
  &lt;!----&gt;
   &lt;div&gt;
      &lt;tabs v-model=&quot;activeKey&quot;&gt;
        &lt;pane label=&quot;&quot; name=&quot;1&quot;&gt;
          
        &lt;/pane&gt;

        &lt;pane label=&quot;&quot; name=&quot;2&quot;&gt;
          
        &lt;/pane&gt;

        &lt;pane label=&quot;&quot; name=&quot;3&quot;&gt;
          
        &lt;/pane&gt;
      &lt;/tabs&gt;
    &lt;/div&gt;
&lt;/template&gt;

&lt;script&gt;
  import &#x27;./demo2.styl&#x27;
  import tabs from &#x27;./tabs.vue&#x27;
  import pane from &#x27;./pane.vue&#x27;
  export default {
    data () {
      return {
        activeKey: &#x27;1&#x27;
      }
    },
    components: {
      tabs,
      pane
    }
  }
&lt;/script&gt;
      </code>
    </pre>

    <h5>tabs.vue</h5>
    <pre v-pre>
      <code>
&lt;template&gt;
  &lt;!-- tabs.vue  --&gt;
  &lt;div class=&quot;tabs&quot;&gt;
    &lt;div class=&quot;tabs-bar&quot;&gt;
      &lt;div v-for=&quot;(item, index) in navList&quot; :class=&quot;tabCls(item)&quot; @click=&quot;handleChange(index)&quot;&gt;
        {{item.label}}
      &lt;/div&gt;
    &lt;/div&gt;

    &lt;div class=&quot;tabs-content&quot;&gt;
      &lt;slot&gt;&lt;/slot&gt;
    &lt;/div&gt;

  &lt;/div&gt;
&lt;/template&gt;

&lt;script&gt;
  export default {
    props: {
      /*
      * &lt;tabs v-model=&quot;activeKey&quot;&gt; v-modelactiveKey
      * value  activeKey
      * */
      value: {
        type: [String, Number]
      }
    },
    data () {
      return {
        // value,
        currentValue: this.value,
        navList: []
      }
    },
    mounted () {
    },
    methods: {
      getTags () {
        // pane
        return this.$children.filter((item) =&gt; {
          return item.$options.name === &#x27;pane&#x27;
        })
      },
      // mounted paneupdateNavnavList
      updateNav () {
        this.navList = []
        this.getTags().forEach((pane, index) =&gt; {
          this.navList.push({
            // label
            label: pane.label,
            // namename
            name: pane.name || index
          })
          // tabTODO 
          if (index === 0) {
            if (!this.currentValue) {
              this.currentValue === pane.name || index
            }
          }
        })
        this.updateStatus()
      },
      updateStatus () {
        var tabs = this.getTags()
        tabs.forEach((tab) =&gt; {
          // tab
          // showshow
          tab.show = tab.name === this.currentValue
        })
      },
      tabCls (item) {
        return [
          &#x27;tabs-tab&#x27;,
          {
            // tabclass
            &#x27;tabs-tab-active&#x27;: item.name === this.currentValue
          }
        ]
      },
      // tab
      handleChange (index) {
        var nav = this.navList[index]
        var name = nav.name
        // tab, watch
        this.currentValue = name
        // value
        this.$emit(&#x27;input&#x27;, name)
        // 
        this.$emit(&#x27;on-click&#x27;, name)
      }
    },
    watch: {
      value (val) {
        this.currentValue = val
      },
      currentValue () {
        // tabpane
        this.updateStatus()
      }
    }
  }
&lt;/script&gt;
      </code>
    </pre>

    <h5>pane.vue</h5>
    <pre v-pre>
      <code>
&lt;template&gt;
  &lt;!-- pane.vue --&gt;
  &lt;div&gt;
    &lt;div class=&quot;pane&quot; v-show=&quot;show&quot;&gt;
      &lt;slot&gt;&lt;/slot&gt;
    &lt;/div&gt;
  &lt;/div&gt;
&lt;/template&gt;

&lt;script&gt;
  export default {
    name: &#x27;pane&#x27;,
    props: {
      name: {
        type: String
      },
      label: {
        type: String,
        default: &#x27;&#x27;
      }
    },
    data () {
      return {
        show: true
      }
    },
    mounted () {
      this.updateNav()
    },
    methods: {
      updateNav () {
        // 
        this.$parent.updateNav()
      }
    }
  }
&lt;/script&gt;
      </code>
    </pre>
  </div>
</template>

<script>
  import './demo2.styl'
  import tabs from './tabs.vue'
  import pane from './pane.vue'
  export default {
    data () {
      return {
        activeKey: '1'
      }
    },
    mounted () {
    },
    methods: {
    },
    components: {
      tabs,
      pane
    }
  }
</script>




