<template>
  <div class="container">
    <h3>vm.$nextTick( [callback] )</h3>
    <ul>
      <li><strong>:</strong>
        <ol>
          <li><code>{Function} [callback]</code></li>
        </ol>

      </li>
      <li><strong>:</strong> <span> DOM  DOM</span></li>
      <li><strong class="warning">:</strong>  <code>vm.$nextTick()</code>  <code>Vue.nextTick</code>  Vue  <code>this</code>  Vue </li>
    </ul>
    <h5></h5>
    <div class="demo-box">
      <p class="warning">{{text}}</p>
      <ul>
        <li v-for="v in listData">{{v}}</li>
      </ul>
      <button @click="updateDate" v-if="!text"></button>
    </div>
    <pre v-pre>
      <code class="html">
&lt;div class=&quot;demo-box&quot;&gt;
  &lt;p class=&quot;warning&quot;&gt;{{text}}&lt;/p&gt;
  &lt;ul&gt;
    &lt;li v-for=&quot;v in listData&quot;&gt;{{v}}&lt;/li&gt;
  &lt;/ul&gt;
  &lt;button @click=&quot;updateDate&quot; v-if=&quot;!text&quot;&gt;&lt;/button&gt;
&lt;/div&gt;
      </code>
    </pre>

    <pre>
      <code class="js">
export default {
    props: [],
    data() {
      return {
        listData: ['', '', ''],
        text: ''
      }
    },
    mounted() {
    },
    methods: {
      updateDate() {
        this.listData = ['', '', '']
        let arr = ['', '', '']
        this.text = '5'
        setTimeout(() => {
          this.listData.push(...arr)
          // DOM
          this.$nextTick( () => {
            // DOM 
            this.updateAfter()
          })
        }, 5000)
      },
      updateAfter() {
        this.text = ''
      }
    },
    components: {},
    watch: {}
  }
      </code>
    </pre>
  </div>

</template>

<script>

  export default {
    props: [],
    data() {
      return {
        listData: ['', '', ''],
        text: ''
      }
    },
    mounted () {
    },
    methods: {
      updateDate() {
        this.listData = ['', '', '']
        let arr = ['', '', '']
        this.text = '5'
        setTimeout(() => {
          this.listData.push(...arr)
          // DOM
          this.$nextTick( () => {
            // DOM 
            this.updateAfter()
          })
        }, 5000)
      },
      updateAfter() {
        this.text = ''
      }
    },
    components: {},
    watch: {}
  }

</script>



