<template>
  <div class="container">
    <h3>directive</h3>
    <ul>
      <li>
        <strong>:</strong>
        <ol>
          <li><code>{string} id</code></li>
          <li><code>{Function | Object} [definition]</code></li>
        </ol>
      </li>
      <li><strong class="warning">:</strong>
        1directivedirectives 2directive  bind  inserted
      </li>
      <li>
        <strong>:</strong>
        <ol>
          <li><code>bind</code></li>
          <li><code>inserted</code> document </li>
          <li><code>update</code> VNode <strong> VNode </strong> ()</li>
          <li><code>componentUpdated</code> VNode <strong> VNode</strong> </li>
          <li><code>unbind</code> </li>
        </ol>
      </li>
      <li>
        <strong></strong>
        <ol>
          <li>el:  DOM </li>
          <li>
            binding: 
            <ol>
              <li><strong>name:</strong>  <code>v-</code> </li>
              <li><strong>value: </strong>  <code>v-my-directive="1 + 1"</code>, value  2</li>
              <li><strong>oldValue: </strong> <code>update</code> <code>componentUpdated</code> </li>
              <li><strong>expression: </strong>  <code>v-my-directive="1 + 1"</code>  <code>expression  "1 + 1"</code></li>
              <li><strong>arg: </strong> <code>v-my-directive:foo arg  "foo"</code></li>
              <li><strong>modifiers: </strong>  <code>v-my-directive.foo.bar</code>,  modifiers  <code>{ foo: true, bar: true }</code></li>
            </ol>
          </li>
          <li>vnode: Vue  <a href="https://cn.vuejs.org/v2/api/#VNode" target="_blank">VNode API</a> </li>
          <li>oldVnode:  update  componentUpdated </li>
        </ol>
      </li>
      <li>
        <strong class="warning">:</strong>
         <code>el</code>  <strong>dataset</strong> 
      </li>
    </ul>
    <h5>1:</h5>
    <pre>
      <code class="js">
 //  v-focus
  Vue.directive('focus', {
    bind() {
      console.log('bind')
    },
    //  DOM 
    inserted () {
      console.log('inserted')
    },
    update () {
      // 
      console.log('update')
    },
    componentUpdated() {
      // 
      console.log('componentUpdated')
    },
    unbind() {
      // 
      console.log('unbind')
    }
  })
      </code>
    </pre>
    <pre>
      <code class="html">
// 
&lt;div v-focus&gt;&lt;/div&gt;
      </code>
    </pre>
  </div>

</template>
<style scoped>

</style>
<script>
  export default {
    props: [],
    data () {
      return {
        demo1: {
          str: ''
        }
      }
    },
    mounted () {
    },
    methods: {},
    components: {},
    watch: {}
  }
</script>



