<template>
  <div class="container">
    <h3>slot</h3>
    <ul>
      <li><strong>:</strong> <span></span></li>
      <li>
        <strong class="warning">:</strong>
        <ol>
          <li><span>slot</span></li>
          <li><span>&lt;slot&gt;</span></li>
        </ol>
      </li>
    </ul>
    <h5>slot</h5>
    <pre v-pre class="mb48">
      <code class="html">
    &lt;template&gt;
    &lt;!--  --&gt;
    &lt;demo1&gt;
      &lt;div&gt;&lt;/div&gt;
      &lt;div&gt;&lt;/div&gt;
    &lt;/demo1&gt;
    &lt;/template&gt;
      </code>
    </pre>
    <pre v-pre>
    <code class="hljs">
    import demo1 from './demo1.vue'
    export default {
      components: {
        demo1
      }
    }
    </code>
    </pre>
    <pre v-pre class="mb48">
    <code class="html">
    &lt;template&gt;
    &lt;!--  --&gt;
    &lt;div&gt;
      &lt;slot&gt;
        &lt;p&gt;{{text1}}&lt;/p&gt;
      &lt;/slot&gt;
    &lt;/div&gt;
    &lt;/template&gt;
    </code>
    </pre>
    <pre>
    <code class="hljs">
    export default {
      props: [],
      data () {
        return {
          text1: ''
        }
      },
      mounted () {
      },
      methods: {},
      components: {},
      watch: {}
    }
    </code>
    </pre>
    <div class="demo-box">
      <p>&lt;slot&gt;</p>
      <demo1>
        <div></div>
        <div></div>
      </demo1>
    </div>

    <h5>&lt;slot&gt;nameSlotSlot</h5>
    <pre v-pre class="mb48">
      <code class="html">
  &lt;template&gt;
    &lt;!--  --&gt;
    &lt;demo2&gt;
      &lt;h2 slot=&quot;header&quot;&gt;&lt;/h2&gt;
      &lt;p&gt;&lt;/p&gt;
      &lt;p&gt;&lt;/p&gt;
      &lt;div slot=&quot;footer&quot;&gt;&lt;/div&gt;
    &lt;/demo2&gt;
  &lt;/template&gt;
      </code>
    </pre>
    <pre v-pre>
      <code class="hljs">
    import demo2 from './demo2.vue'
    export default {
      components: {
        demo2
      }
    }
      </code>
    </pre>
    <pre v-pre>
      <code class="html">
  &lt;template&gt;
    &lt;!--  --&gt;
    &lt;div&gt;
      &lt;div class=&quot;header&quot;&gt;
        &lt;slot name=&quot;header&quot;&gt;&lt;/slot&gt;
      &lt;/div&gt;

      &lt;div&gt;
        &lt;slot&gt;&lt;/slot&gt;
      &lt;/div&gt;

      &lt;div&gt;
        &lt;slot name=&quot;footer&quot;&gt;&lt;/slot&gt;
      &lt;/div&gt;

    &lt;/div&gt;
  &lt;/template&gt;
    </code>
    </pre>
    <div class="demo-box">
      <demo2>
        <h2 slot="header"></h2>
        <p></p>
        <p></p>
        <div slot="footer"></div>
      </demo2>
    </div>

    <h5>, slot-scopescope</h5>
    <pre v-pre class="mb48">
      <code class="html">
  &lt;template&gt;
    &lt;!--  --&gt;
     &lt;demo3&gt;
        &lt;div slot-scope=&quot;props&quot;&gt;
          &lt;p&gt;&lt;/p&gt;
          &lt;p&gt;{{props.msg}}&lt;/p&gt;
        &lt;/div&gt;
      &lt;/demo3&gt;
  &lt;/template&gt;
      </code>
    </pre>
    <pre v-pre>
      <code class="hljs">
    import demo3 from './demo3.vue'
    export default {
      components: {
        demo3
      }
    }
      </code>
    </pre>
    <pre v-pre class="mb48">
      <code class="html">
    &lt;template&gt;
      &lt;!--  --&gt;
      &lt;div&gt;
        &lt;slot :msg=&quot;childMsg&quot;&gt;&lt;/slot&gt;
      &lt;/div&gt;
    &lt;/template&gt;
    </code>
    </pre>
    <pre>
      <code class="hljs">
    export default {
      props: [],
      data () {
        return {
          childMsg: '!!'
        }
      }
    }
      </code>
    </pre>
    <div class="demo-box">
      <demo3>
        <div slot-scope="props">
          <p></p>
          <p>{{props.msg}}</p>
        </div>
      </demo3>
    </div>


    <h5>: Slot</h5>
    <pre v-pre class="mb48">
      <code class="html">
  &lt;template&gt;
    &lt;!--  --&gt;
     &lt;demo4 :books=&quot;books&quot;&gt;
        &lt;!-- Slot --&gt;
        &lt;template slot=&quot;fanfan&quot; slot-scope=&quot;props&quot;&gt;
          &lt;li&gt;{{props.bookCname}} --- {{props.bookName}}&lt;/li&gt;
        &lt;/template&gt;
      &lt;/demo4&gt;
  &lt;/template&gt;
      </code>
    </pre>
    <pre v-pre>
      <code class="hljs">
    import demo4 from './demo4.vue'
    export default {
      data () {
        return {
          books: [
            {
              name: 'vue.js',
              cname: ''
            },
            {
              name: 'javaScript ',
              cname: ''
            },
            {
              name: 'vue.js ',
              cname: ''
            }
          ]
        }
      },
      components: {
        demo4
      }
    }
      </code>
    </pre>
    <pre v-pre class="mb48">
      <code class="html">
    &lt;template&gt;
      &lt;!--  --&gt;
      &lt;div&gt;
        &lt;ul&gt;
          &lt;slot name=&quot;fanfan&quot; v-for=&quot;fanfan in books&quot; :book-cname=&quot;fanfan.cname :book-name="fanfan.name"&quot;&gt;
            &lt;!--  slot  --&gt;
          &lt;/slot&gt;
        &lt;/ul&gt;
       &lt;/div&gt;
    &lt;/template&gt;
    </code>
    </pre>
    <pre>
      <code class="hljs">
    export default {
      props: ['books'],
      data () {
        return {
        }
      }
    }
      </code>
    </pre>
    <div class="demo-box">
      <demo4 :books="books">
        <!-- Slot -->
        <template slot="fanfan" slot-scope="props">
          <li>{{props.bookCname}} --- {{props.bookName}}</li>
        </template>
      </demo4>
    </div>



  </div>
</template>

<style scoped>

</style>

<script>
  import demo1 from './demo1.vue'
  import demo2 from './demo2.vue'
  import demo3 from './demo3.vue'
  import demo4 from './demo4.vue'
  export default {
    data () {
      return {
        books: [
          {
            name: 'vue.js',
            cname: ''
          },
          {
            name: 'javaScript ',
            cname: ''
          },
          {
            name: 'vue.js ',
            cname: ''
          }
        ]
      }
    },
    components: {
      demo1,
      demo2,
      demo3,
      demo4,
    }
  }
</script>



