<template>
  <div class="container">
    <h3>$children</h3>
    <ul>
      <li><strong>:</strong> <span></span></li>
      <li><strong class="warning">:</strong> <code>$children ref$refs $children</code></li>
    </ul>
<h5></h5>
<pre v-pre class="mb48">
  <code class="html">
&lt;template&gt;
  &lt;div class=&quot;demo-box&quot;&gt;
      &lt;!--  --&gt;
      &lt;demo&gt;&lt;/demo&gt;
      &lt;button @click=&quot;handlerEvent&quot;&gt;&lt;/button&gt;
  &lt;/div&gt;
&lt;/template&gt;
  </code>
</pre>
<pre>
  <code class="hljs">
  import demo from './demo.vue'
  export default {
    methods: {
      handlerEvent () {
        this.$children.forEach((item, index) => {
          item.message = ''
        })
      }
    },
    components: {
      demo
    }
  }
  </code>
</pre>

<pre v-pre class="mb48">
  <code class="html">
&lt;template&gt;
  &lt;!--  --&gt;
  &lt;div&gt;
    {{message}}
  &lt;/div&gt;
&lt;/template&gt;
  </code>
</pre>

<pre>
  <code class="hljs">
  export default {
    data () {
      return {
        message: ''
      }
    }
  }
  </code>
</pre>


    <div class="demo-box">
      <!--  -->
      <demo></demo>
      <button @click="handlerEvent"></button>
    </div>

  </div>
</template>

<style scoped>

</style>

<script>
  import demo from './demo.vue'
  export default {
    methods: {
      handlerEvent () {
        this.$children.forEach((item, index) => {
          item.message = ''
        })
      }
    },
    components: {
      demo
    }
  }
</script>



