<template>
  <div>
    <p>total :{{this.value}}</p>
    <button @click="add"></button>
  </div>
</template>

<script>
  export default {
    props: ['value'],
    data () {
      return {
        counter: 0
      }
    },
    methods: {
      add () {
        this.counter++
        /*
        * <demo5 v-model="total"></demo5> total
        *  this.$emit('input', this.counter) total
        * */
        this.$emit('input', this.counter)
      }
    }
  }
</script>



