<template>
  <div>

    <ul>
      <li v-for="v in arr"> {{v}}</li>
    </ul>

    <button @click="onPush">push</button>
    <button @click="onPop">pop</button>
    <button @click="onShift">shift</button>
    <button @click="onUnshift">unshift</button>
    <button @click="onSpliceAdd">splice </button>
    <button @click="onSpliceDel">splice </button>
    <button @click="onSort">sort</button>
    <button @click="onReverse">reverse</button>
  </div>
</template>

<style scoped lang="stylus">
  button
    padding: 10px
</style>

<script>
  export default {
    name: 'demo1',
    props: [],
    data () {
      return {
        arr: ['', '', '', '', '', '' ,'', '', ' ', '']
      }
    },
    mounted() {
    },
    methods: {
      onPush () {
        this.arr.push('')
      },
      onPop () {
        this.arr.pop()

      },
      onShift () {
        this.arr.shift()
      },
      onUnshift () {
        this.arr.unshift('', '', '')
      },
      onSpliceAdd () {
        this.arr.splice(1, 2, '')
      },
      onSpliceDel () {
        this.arr.splice(1, 4)
      },
      onSort () {
        this.arr.sort()
      },
      onReverse () {
        this.arr.reverse()
      }
    },
    components: {},
    watch: {}
  }
</script>



