<template>
  <div class="container">
    <h3>extends</h3>
    <ul>
      <li><strong>:</strong><code>Object | Function</code></li>
      <li>
        <strong></strong>
        <ol>
          <li>() <code>Vue.extend</code></li>
          <li> <code>mixins</code> </li>
          <li class="warning">extends</li>

        </ol>
      </li>
    </ul>

    <h5>: element-ui</h5>
 <pre v-pre>
   <code>
     // element-ui node_modulestransferelement-ui
     import newTransfer from &#x27;element-ui/packages/transfer/src/main.vue&#x27;;
     let newComponent = {
       extends: newTransfer
     }
     export default {
       components: {
         newComponent
        }
     }
   </code>
 </pre>
    <h5></h5>
    <pre v-pre>
      <code>
&lt;template&gt;
  &lt;div&gt;
    &lt;new-component&gt;&lt;/new-component&gt;
  &lt;/div&gt;
&lt;/template&gt;

&lt;script&gt;
  import testComponent from &#x27;./extends-component.vue&#x27;

  let newComponent = {
    extends: testComponent,
    computed: {
      count: function () {
        this.str = &#x27;extendscomputedcount&#x27;
        return this.str
      }
    },
    methods: {
      handlerClick () {
        this.clickStr = &#x27;extends methods&#x27;
      }
    }
  }

  export default {
    components: {
      newComponent
    }
  }
&lt;/script&gt;

&lt;style scoped&gt;

&lt;/style&gt;

&lt;!-- extends-component.vue --&gt;
&lt;template&gt;
  &lt;div&gt;
    &lt;p&gt;computeddata&lt;/p&gt;
    &lt;p&gt;{{count}}&lt;/p&gt;
    &lt;p&gt;{{sum}}&lt;/p&gt;
    &lt;br&gt;&lt;br&gt;
    &lt;button type=&quot;text&quot; @click=&quot;handlerClick&quot;&gt;&lt;/button&gt;
    &lt;span&gt;{{clickStr}}&lt;/span&gt;

  &lt;/div&gt;
&lt;/template&gt;

&lt;script&gt;
  export default {
    data () {
      return {
        str: &#x27;&#x27;,
        clickStr: &#x27;&#x27;
      }
    },
    computed: {
      count: function () {
        this.str = &#x27;computed&#x27;
        return this.str
      },
      sum: function () {
        return &#x27;computed&#x27;
      }
    },
    mounted () {
    },
    methods: {
      handlerClick () {
        this.clickStr = &#x27;&#x27;
      }
    },
    watch: {}
  }
&lt;/script&gt;

&lt;style scoped&gt;

&lt;/style&gt;

      </code>
    </pre>
    <div class="demo-box">
      <demon-component></demon-component>
    </div>
  </div>
</template>

<script>
  import demonComponent from './demo1.vue'
  export default {
    name: 'index',
    components: {
      demonComponent
    },
    props: [],
    data () {
      return {}
    },
    mounted () {
    },
    methods: {},
    watch: {}
  }
</script>

<style scoped>

</style>



