<template>
  <div>
    <p>computeddata</p>
    <p>{{count}}</p>
    <p>{{sum}}</p>
    <br><br>
    <button type="text" @click="handlerClick"></button>
    <span>{{clickStr}}</span>

  </div>
</template>

<script>
  export default {
    data () {
      return {
        str: '',
        clickStr: ''
      }
    },
    computed: {
      count: function () {
        this.str = 'computed'
        return this.str
      },
      sum: function () {
        return 'computed'
      }
    },
    mounted () {
    },
    methods: {
      handlerClick () {
        this.clickStr = ''
      }
    },
    watch: {}
  }
</script>

<style scoped>

</style>



