<template>
  <div>
    <!-- tab1 -->
    <button @click="updataText">tab1</button>
    <p>{{message}}</p>
  </div>
</template>

<script>
  export default {
    data () {
      return {
        message: ''
      }
    },
    methods: {
      updataText () {
        this.message = 'tab1'
      }
    }
  }
</script>



