<template>
  <div class="container">
    <h3>vvm.$mount( [elementOrSelector] )</h3>
    <ul>
      <li><strong>:</strong>
        <ol>
          <li><code>{Element | string} [elementOrSelector]</code></li>
          <li><code>{boolean} [hydrating]</code></li>
        </ol>

      </li>
      <li><strong>:</strong> <span> Vue  el  DOM  <code>vm.$mount()</code> </span></li>
      <li> <code>elementOrSelector</code>  DOM API </li>
    </ul>

    <pre v-pre>
      <code>
var MyComponent = Vue.extend({
  template: '&lt;div&gt;Hello!&lt;/div&gt;'
})

//  #app ( #app)
new MyComponent().$mount('#app')

// 
new MyComponent({ el: '#app' })

// 
var component = new MyComponent().$mount()
document.getElementById('app').appendChild(component.$el)

      </code>
    </pre>
  </div>

</template>

<script>

  export default {
    props: [],
    data() {
      return {
        listData: ['', '', ''],
        text: ''
      }
    },
    mounted() {
    },
    methods: {
      updateDate() {
        this.listData = ['', '', '']
        let arr = ['', '', '']
        this.text = '5'
        setTimeout(() => {
          this.listData.push(...arr)
          // DOM
          this.$nextTick( () => {
            // DOM 
            this.updateAfter()
          })
        }, 5000)


      },
      updateAfter() {
        this.text = ''
      }
    },
    components: {},
    watch: {}
  }

</script>



