<template>
  <div class="container">
    <h3>$refs </h3>
    <ul>
      <li><strong>:</strong> <span>$refs ,ref</span></li>
      <li><strong class="warning">:</strong> <code>$refs$refs</code></li>
    </ul>
    <h5></h5>
    <pre class="mb48" v-pre>
      <code class="html">
&lt;template&gt;
    &lt;div class=&quot;demo-box&quot;&gt;
      &lt;!--  --&gt;
      &lt;demo ref=&quot;demo1&quot;&gt;&lt;/demo&gt;
      &lt;button @click=&quot;handlerEvent&quot;&gt;&lt;/button&gt;
    &lt;/div&gt;
&lt;/template&gt;
      </code>
    </pre>
    <pre>
      <code class="hljs">
  import demo from &#x27;./demo.vue&#x27;
  export default {
    methods: {
      handlerEvent () {
        this.$refs.demo1.message = &#x27;&#x27;
      }
    },
    components: {
      demo
    }
  }
      </code>
    </pre>

    <pre class="mb48" v-pre>
      <code class="html">
&lt;template&gt;
  &lt;!--  --&gt;
  &lt;div&gt;
    {{message}}
  &lt;/div&gt;
&lt;/template&gt;
      </code>
    </pre>
    <pre>
      <code class="hljs">
  export default {
    data () {
      return {
        message: ''
      }
    }
  }
      </code>
    </pre>

    <div class="demo-box">
      <!--  -->
      <demo ref="demo1"></demo>
      <button @click="handlerEvent"></button>
    </div>
  </div>
</template>

<style scoped>

</style>

<script>
  import demo from './demo.vue'
  export default {
    methods: {
      handlerEvent () {
        this.$refs.demo1.message = ''
      }
    },
    components: {
      demo
    }
  }
</script>



