<template>
  <div class="container">
    <h3>watch | vm.$watch</h3>
    <ul>
      <li><strong></strong>
        <ol>
          <li><code>{ [key: string]: string | Function | Object | Array }</code></li>
          <li><code>deep:</code>  deep: true </li>
          <li><code>immediate</code>  immediate: true </li>
        </ol>
      </li>
      <li><strong>:</strong> <span>Vue  <code>$watch()</code> <code>watch</code> </span></li>
      <li><code>vm.$watch</code>  <code>watch</code> </li>
      <li><span class="warning">:</span> watcher </li>
    </ul>
    <h5></h5>
    <pre v-pre>
        <code>
 var vm = new Vue({
  data: {
    a: 1,
    b: 2,
    c: 3,
    d: 4,
    e: {
      f: {
        g: 5
      }
    }
  },
  watch: {
    a: function (val, oldVal) {
      console.log('new: %s, old: %s', val, oldVal)
    },
    // 
    b: 'someMethod',
    //  watcher
    c: {
      handler: function (val, oldVal) { /* ... */ },
      deep: true
    },
    // 
    d: {
      handler: function (val, oldVal) { /* ... */ },
      immediate: true
    },
    e: [
      function handle1 (val, oldVal) { /* ... */ },
      function handle2 (val, oldVal) { /* ... */ }
    ],
    // watch vm.e.f's value: {g: 5}
    'e.f': function (val, oldVal) { /* ... */ }
  }
})
vm.a = 2 // => new: 2, old: 1
        </code>
      </pre>
    <h5></h5>
    <pre v-pre>
      <code>
// 
vm.$watch('a.b.c', function (newVal, oldVal) {
  // 
})

// 
vm.$watch(
  function () {
    return this.a + this.b
  },
  function (newVal, oldVal) {
    // 
  }
)

// vm.$watch 
var unwatch = vm.$watch('a', cb)
// 
unwatch()
      </code>
    </pre>
    <h5></h5>
    <pre v-pre>
      <code>
&lt;template&gt;
  &lt;div&gt;
    &lt;input type=&quot;text&quot; v-model=&quot;str1&quot; style=&quot;width: 200px&quot;&gt;
    &lt;p&gt;{{watchStr1}}&lt;/p&gt;
    &lt;br&gt;&lt;br&gt;
    &lt;input type=&quot;text&quot; v-model=&quot;str2&quot; style=&quot;width: 200px&quot;&gt;
    &lt;br&gt;&lt;br&gt;
    &lt;div&gt;
      &lt;p&gt;valoldVal&lt;/p&gt;
      &lt;p&gt;&lt;code&gt;val&lt;/code&gt;: {{str2Val}}&lt;/p&gt;
      &lt;p&gt;&lt;code&gt;oldVal&lt;/code&gt;: {{str2OldVal}}&lt;/p&gt;
    &lt;/div&gt;
    &lt;br&gt;&lt;br&gt;
    &lt;p&gt;josn&lt;/p&gt;
    &lt;input type=&quot;text&quot; v-model=&quot;str3Json.a&quot;&gt;
    &lt;p&gt;{{str3Json.b}}&lt;/p&gt;
  &lt;/div&gt;
&lt;/template&gt;

&lt;script&gt;
  export default {
    name: &#x27;demo1&#x27;,
    props: [],
    data () {
      return {
        str1: &#x27;watch&#x27;,
        watchStr1: &#x27;&#x27;,
        str2: &#x27;&#x27;,
        str2Val: &#x27;&#x27;,
        str2OldVal: &#x27;&#x27;,
        str3Json: {
          a: &#x27;json&#x27;,
          b: &#x27;&#x27;
        }
      }
    },
    mounted () {
    },
    methods: {
      watchStr1Fn () {
        this.watchStr1 = this.str1
      }
    },
    watch: {
      &#x27;str1&#x27;: &#x27;watchStr1Fn&#x27;,
       str2: function (val, oldVal) {
        this.str2Val = val
        this.str2OldVal = oldVal
      },
      &#x27;str3Json.a&#x27;: function (val, oldVal) {
        this.str3Json.b = this.str3Json.a
      }

    }
  }
&lt;/script&gt;

&lt;style scoped&gt;

&lt;/style&gt;

      </code>
    </pre>
    <div class="demo-box">
      <demo1></demo1>
    </div>
  </div>

</template>

<script>
  import demo1 from './demo1.vue'
  export default {
    props: [],
    components: {
      demo1
    },
    data () {
      return {}
    },
    mounted () {
    },
    methods: {
    },
    watch: {}
  }
</script>



