<template>
  <div>
    <p class="info1">inputinput</p>
    <template v-if="type === 'name' ">
      <label></label>
      <input type="text" placeholder="">
    </template>

    <template v-else>
      <label></label>
      <input type="text" placeholder="">
    </template>
    <button @click="handleToggleClick"></button>
  </div>
</template>

<script>
  export default {
    props: [],
    data () {
      return {
        type: 'name'
      }
    },
    mounted () {
    },
    methods: {
      handleToggleClick () {
        this.type = this.type === 'name' ? 'mall' : 'name'
      }
    },
    components: {},
    watch: {}
  }
</script>

<style scoped>

</style>



