<template>
  <div class="container">
    <h3>Vue.component( id, [definition] )  components</h3>
    <ul>
      <li><strong>:</strong>
        <ol>
          <li><code>{string} id</code></li>
          <li><code>{Function | Object} [definition]</code></li>
        </ol>
      </li>
      <li><strong>:</strong> <span> <code>id</code></span></li>
      <li><strong class="warning">:</strong>props"v-on":@, $emit</li>
      <li><strong class="warning">:</strong><code>Vue.component</code> <code>components</code></li>
      <li>
         props down, events up props  events 
        <p class="pic"></p>
      </li>
    </ul>

    <h5>1is</h5>
    <pre style="margin-bottom: 0; padding-bottom: 0">
      <code class="html">
&lt;div id=&quot;app&quot;&gt;
  &lt;tabel&gt;
    &lt;tbody is=&quot;my-component&quot;&gt;&lt;/tbody&gt;
  &lt;/tabel&gt;
&lt;/div&gt;
      </code>
    </pre>
    <pre>
      <code class="hljs">
Vue.component('my-component', {
  template: '&lt;div&gt;&lt;/div&gt;'
})
let app =new Vue({
  el: '#app'
})
      </code>
    </pre>

    <h5>2, Vue.component</h5>
<pre v-pre>
  <code>
&lt;div id=&quot;app&quot;&gt;
  &lt;p&gt;{{count}}&lt;/p&gt;
  &lt;child-component :count=&quot;1&quot;&gt;&lt;/child-component&gt;
&lt;/div&gt;

Vue.component('child-component', {
  'name': 'child-component',
   props: {
    count: {
      type: Number,
      default: 1
    }
   },
   template: `
     &lt;div class=&quot;child&quot;&gt;
       &lt;p&gt;{{count}}&lt;/p&gt;
       &lt;child-component :count=&quot;count + 1&quot; v-if=&quot;count &lt; 3&quot;&gt;&lt;/child-component&gt;
     &lt;/div&gt;
   `
});

new new({
  el: '#app'
})
  </code>
</pre>

    <h5>3(components)</h5>
    <p class="orange">&lt;component&gt;is</p>
    <pre>
      <code v-pre>
&lt;template&gt;
  &lt;button @click=&quot;updateComponent(&#x27;firstTab1&#x27;)&quot;&gt;&lt;/button&gt;
  &lt;button @click=&quot;updateComponent(&#x27;tab2&#x27;)&quot;&gt;&lt;/button&gt;
  &lt;button @click=&quot;updateComponent(&#x27;tab3&#x27;)&quot;&gt;&lt;/button&gt;
  &lt;component :is=&quot;currentView&quot;&gt;&lt;/component&gt;
&lt;/template&gt;

&lt;script&gt;
  import tab1 from './tab1.vue'
  import tab2 from './tab2.vue'
  import tab3 from './tab3.vue'
  export default {
    data () {
      return {
        currentView: 'firstTab1'
      }
    },
    methods: {
      updateComponent (name) {
        this.currentView = name
      }
    },
    components: {
      // 
      'firstTab1': tab1,
      tab2,
      tab3
    }
  }
&lt;/script&gt;
      </code>
    </pre>
    <pre v-pre>
      <code>
&lt;!-- tab1 --&gt;
&lt;template&gt;
  &lt;div&gt;
    &lt;button @click=&quot;updataText&quot;&gt;tab1&lt;/button&gt;
    &lt;p&gt;{{message}}&lt;/p&gt;
  &lt;/div&gt;
&lt;/template&gt;

&lt;script&gt;
export default {
    data () {
      return {
        message: ''
      }
    },
    methods: {
      updataText () {
        this.message = 'tab1'
      }
    }
  }
&lt;/script&gt;
      </code>
    </pre>
    <pre v-pre>
      <code>
&lt;!-- tab2 --&gt;
&lt;template&gt;
  &lt;div&gt;
    &lt;button @click=&quot;updataText&quot;&gt;tab2&lt;/button&gt;
    &lt;p&gt;{{message}}&lt;/p&gt;
  &lt;/div&gt;
&lt;/template&gt;

&lt;script&gt;
export default {
    data () {
      return {
        message: ''
      }
    },
    methods: {
      updataText () {
        this.message = 'tab2'
      }
    }
  }
&lt;/script&gt;
      </code>
    </pre>
    <pre v-pre>
      <code>
&lt;!-- tab3 --&gt;
&lt;template&gt;
  &lt;div&gt;
    &lt;button @click=&quot;updataText&quot;&gt;tab3&lt;/button&gt;
    &lt;p&gt;{{message}}&lt;/p&gt;
  &lt;/div&gt;
&lt;/template&gt;

&lt;script&gt;
export default {
    data () {
      return {
        message: ''
      }
    },
    methods: {
      updataText () {
        this.message = 'tab3'
      }
    }
  }
&lt;/script&gt;
      </code>
    </pre>

    <div class="demo-box">
      <button @click="updateComponent('firstTab1')"></button>
      <button @click="updateComponent('tab2')"></button>
      <button @click="updateComponent('tab3')"></button>
      <br><br>
      <component :is="currentView"></component>
    </div>

    <h5>3</h5>
 <pre v-pre>
   <code>
&lt;div id=&quot;app&quot;&gt;
   &lt;child-component&gt;&lt;/child-component&gt;
&lt;/div&gt;

&lt;script&gt;
Vue.componet('child-component', function (resolve, reject) {
  window.setTimeout(function () {
    resolve({
      template: '&lt;div&gt;&lt;/div&gt;'
    })
  }, 2000)
});

var app = new vue({
  el: '#app'
})

&lt;/script&gt;
   </code>
 </pre>
  </div>
</template>
<style scoped>
.pic {
  width: 300px;
  height: 300px;
  background: url('./props-events.png') no-repeat;
  background-size: contain;
}
</style>
<script>
  import tab1 from './tab1.vue'
  import tab2 from './tab2.vue'
  import tab3 from './tab3.vue'
  export default {
    props: [],
    data () {
      return {
        counter: 1,
        myMessage: '',
        currentView: 'firstTab1'
      }
    },
    methods: {
      updateComponent (name) {
        this.currentView = name
      }
    },
    components: {
      // 
      'firstTab1': tab1,
      tab2,
      tab3
    }
  }
</script>



