<template>
  <div class="tabs">
    <div class="tabs-bar">
      <div v-for="(item, index) in navList" :class="tabCls(item)" @click="handleChange(index)">
        {{item.label}}
      </div>
    </div>

    <div class="tabs-content">
      <slot></slot>
    </div>

  </div>
</template>

<script>
  export default {
    props: {
      /*
      * <tabs v-model="activeKey"> v-modelactiveKey
      * value  activeKey
      * */
      value: {
        type: [String, Number]
      }
    },
    data () {
      return {
        // value,
        currentValue: this.value,
        navList: []
      }
    },
    mounted () {
    },
    methods: {
      getTags () {
        // pane
        return this.$children.filter((item) => {
          return item.$options.name === 'pane'
        })
      },
      // mounted paneupdateNavnavList
      updateNav () {
        this.navList = []
        this.getTags().forEach((pane, index) => {
          this.navList.push({
            // label
            label: pane.label,
            // namename
            name: pane.name || index
          })
          // tabTODO 
          if (index === 0) {
            if (!this.currentValue) {
              this.currentValue === pane.name || index
            }
          }
        })
        this.updateStatus()
      },
      updateStatus () {
        var tabs = this.getTags()
        tabs.forEach((tab) => {
          // tab
          // showshow
          tab.show = tab.name === this.currentValue
        })
      },
      tabCls (item) {
        return [
          'tabs-tab',
          {
            // tabclass
            'tabs-tab-active': item.name === this.currentValue
          }
        ]
      },
      // tab
      handleChange (index) {
        var nav = this.navList[index]
        var name = nav.name
        // tab, watch
        this.currentValue = name
        // value
        this.$emit('input', name)
        // 
        this.$emit('on-click', name)
      }
    },
    watch: {
      value (val) {
        this.currentValue = val
      },
      currentValue () {
        // tabpane
        this.updateStatus()
      }
    }
  }
</script>



