<template>
  <div class="container">
    <h3>v-for</h3>
    <ul>
      <li v-pre>:<code>/</code></li>
    </ul>
    <h5></h5>
    <pre v-pre>
      <code class="html">
&lt;ul&gt;
  &lt;li v-for=&quot;v in names&quot;&gt;{{v.name}}&lt;/li&gt;
&lt;/ul&gt;

 &lt;ul&gt;
  &lt;li v-for=&quot;(v, index) in names&quot;&gt;{{index}}---{{v.name}}&lt;/li&gt;
&lt;/ul&gt;

&lt;ul&gt;
  &lt;template v-for=&quot;(v, index) in names&quot;&gt;
    &lt;li&gt;{{index}}---{{v.name}}&lt;/li&gt;
    &lt;li&gt;{{index}}---{{v.name}}&lt;/li&gt;
  &lt;/template&gt;
&lt;/ul&gt;
      </code>
    </pre>
    <pre>
      <code class="js">
data () {
  return {
    names: [
      {name: ''},
      {name: ''},
      {name: ''},
    ]
  }
},
      </code>
    </pre>

    <h5></h5>
    <pre v-pre>
      <code class="html">
&lt;ul&gt;
  &lt;li v-for=&quot;value in user&quot;&gt;{{value}}&lt;/li&gt;
&lt;/ul&gt;

&lt;!--  --&gt;
&lt;ul&gt;
  &lt;li v-for=&quot;(value, key, index) in user&quot;&gt;
    {{value}} {{key}} {{index}}
  &lt;/li&gt;
&lt;/ul&gt;
      </code>
    </pre>
    <pre>
      <code class="js">
data () {
  return {
    user: {
      name: '',
      gender: '',
      age: '23'
    }
  }
},
      </code>
    </pre>
  </div>

</template>

<script>
  export default {
    props: [],
    data () {
      return {
        user: {
          name: '',
          gender: '',
          age: '23'
        }
      }
    },
    mounted () {
    },
    methods: {
    },
    components: {},
  }
</script>

<style scoped>

</style>



