<template>
  <div class="container">
    <h3>props</h3>
    <ul>
      <li><strong>:</strong>
        <ol>
          <li><code>Array string | object</code></li>
        </ol>
      </li>
      <li><strong>:</strong> <span> <code>id</code></span></li>
      <li><strong class="warning">:</strong>props  <span class="warning">(camelCase)props(kebab-case)</span></li>
      <li><code>vue2props</code></li>
    </ul>

    <h5>: </h5>

    <pre v-pre class="mb48">
      <code class="html">
&lt;!-- --&gt;
&lt;input type=&quot;text&quot; v-model=&quot;warningText&quot;&gt;
&lt;my-component :warning-text=&quot;warningText&quot;&gt;&lt;/my-component&gt;
      </code>
    </pre>

    <pre>
      <code class="hljs">
  import myComponent from './myComponent.vue'
  export default {
    name: 'index',
    props: [],
    data () {
      return {
        warningText: 'props'
      }
    },
    components: {
      myComponent
    }
  }
      </code>
    </pre>

    <pre v-pre class="mb48">
      <code class="html">
&lt;!-- --&gt;
&lt;div&gt;
  &lt;p&gt;{{warningText}}&lt;/p&gt;
&lt;/div&gt;
      </code>
    </pre>
    <pre>
      <code class="hljs">
// 
export default {
  name: 'my-component',
  props: ['warningText']
}
      </code>
    </pre>

    <div class="demo-box">
      <input type="text" v-model="warningText">
      <my-component :warning-text="warningText"></my-component>
    </div>
  </div>
</template>

<style scoped>

</style>

<script>
  import myComponent from './myComponent.vue'
  export default {
    name: 'index',
    props: [],
    data () {
      return {
        warningText: 'props'
      }
    },
    components: {
      myComponent
    }
  }
</script>



